import Joi from "joi";

export const contactSchema = Joi.object({
    name: Joi.string()
        .min(3)
        .required()
        .messages({
            "string.base": "El nombre debe ser un texto",
            "string.empty": "El nombre no puede estar vaco",
            "string.min": "El nombre debe tener al menos 3 caracteres",
            "any.required": "El nombre es obligatorio"
        }),

    relation: Joi.string()
        .min(3)
        .required()
        .messages({
            "string.base": "El parentesco debe ser un texto",
            "string.empty": "El parentesco no puede estar vaco",
            "string.min": "El parentesco debe tener al menos 3 caracteres",
            "any.required": "El parentesco es obligatorio"
        }),

        
    phone: Joi.string()
    .pattern(/^(?:\+?\d{1,3})?\s?\(?\d{3}\)?[-.\s]?\d{3}[-.\s]?\d{4}$/)
    .required()
    .messages({
        "string.pattern.base": "El nmero de telfono no es vlido",
        "string.empty": "El nmero de telfono no puede estar vaco",
        "any.required": "El nmero de telfono es obligatorio"
    }),

    otherPhone: Joi.string()
    .pattern(/^(?:\+?\d{1,3})?\s?\(?\d{3}\)?[-.\s]?\d{3}[-.\s]?\d{4}$/)
    .optional()
    .allow(null, '')
    .messages({
        "string.pattern.base": "El nmero de telfono (segundo) no es vlido",
    })
});


export const studySchema = Joi.object({

    grade: Joi.string()
        .min(6)
        .required()
        .messages({
            "string.base": "El estudio debe ser un texto",
            "string.empty": "El estudio no puede estar vaco",
            "any.required": "El estudio es obligatorio"
        }),

    place: Joi.string()
        .min(3)
        .required()
        .messages({
            "string.base": "El centro de estudio debe ser un texto",
            "string.empty": "El centro de estudio no puede estar vaco",
            "string.min": "El centro de estudio debe tener al menos 3 caracteres",
            "any.required": "El centro de estudio es obligatorio"
        }),
        
        age: Joi.number()
        .positive()
        .min(1950)
        .required()
        .messages({
            "number.base": "El ao de graduacin debe ser un nmero vlido",
            "number.positive": "El ao de graduacin debe ser un nmero positivo",
            "number.min": "El ao de graduacin no puede ser menor a 1950",
            "any.required": "El ao de graduacin es obligatorio"
        })
});

export const userContactSchema = Joi.object({
    telefono: Joi.string()
    .pattern(/^(?:\+?\d{1,3})?\s?\(?\d{3}\)?[-.\s]?\d{3}[-.\s]?\d{4}$/)
    .allow("", null)
    .messages({
        "string.pattern.base": "El nmero de telfono no es vlido",
    }),

    celular: Joi.string()
    .pattern(/^(?:\+?\d{1,3})?\s?\(?\d{3}\)?[-.\s]?\d{3}[-.\s]?\d{4}$/)
    .allow("", null)
    .messages({
        "string.pattern.base": "El nmero de celular no es vlido",
    })
});

export const arraysAreEqual = (arr1, arr2) =>
    arr1?.length === arr2?.length &&
    [...arr1 ?? []].sort().every((val, index) => val === [...arr2 ?? []].sort()[index]);
